.popup--back{
	position: absolute;
	width:100%;
	height:100%;
	top:0;
	left:0;
	background-color:rgba(0,0,0,0.7);
	z-index: 4;
}

.popup--content{
	width:60%;
	height:400px;
	background-color: white;
	border-radius: 8px;
	position:relative;
	left:50%;
	top:50%;
	transform: translate(-50%,-50%);
	text-align: center;
}

.popup--close{
	font-weight: bold;
	font-size: 30px;
	position:absolute;
	right:10px;
	cursor:pointer;
	padding:10px;
}
p{
	font-family: fantasy;
}

